* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.country-list li {
  display: flex;
  text-align: center;
}

.country__name {
  text-align: center;
  vertical-align: middle;
}

.country__appearance {
  display: flex;
  align-items: center;
}

.counrty__heading-name {
  margin-left: 5px;
}

.country__full-info-el {
  display: flex;
  align-items: center;
}

.language-list {
  display: flex;
}

.language-list__element:not(:last-child):after {
  content: ',';
}
